<template>
  <el-container class="myhospital dailylistingIn">
    <el-header style="height:76px;">
      <div class="grid-content fonttop">
        <span>就诊人：</span>
        <span>{{from.name}}</span>
        <span style="margin:10px">{{from.nbr}}</span>
        <span style="float: right;line-height: 15px;">
          <el-button class="cut">切换</el-button>
        </span>
      </div>
      <div class="In-time">
        <el-row :gutter="20">
          <el-col :span="4">
            <div class="grid-content bg-purple arrow">
              <button type="button" @click="left($event)" :disabled="disLeft" class="btn-prev">
                <i class="el-icon el-icon-arrow-left" :class="[borLeft]"></i>
              </button>
            </div>
          </el-col>
          <el-col :span="16">
            <div class="grid-content bg-purple arrow">
              <span>{{inTime}}</span>
            </div>
          </el-col>
          <el-col :span="4">
            <div class="grid-content bg-purple arrow">
              <button type="button" @click="right($event)" :disabled="disRight" class="btn-prev">
                <i class="el-icon el-icon-arrow-right" :class="[borRight]"></i>
              </button>
            </div>
          </el-col>
        </el-row>
      </div>
    </el-header>
    <el-main class="main">
      <!--  -->
      <div v-for="dataOnes in dataOne" :key="dataOnes.index" ref="msgDiv">
        <el-card class="box-card">
          <el-row>
            <el-col :span="8">
              <div class="grid-content bg-purple"></div>
            </el-col>
            <el-col :span="8">
              <div class="grid-content bg-purple-light">
                <p style="color:#FF7800;font-size:22px;">
                  ￥
                  <span>{{index}}</span>
                </p>
                <p class="myhospital_time_ft">预交金余额</p>
              </div>
            </el-col>
            <el-col :span="8">
              <div class="grid-content bg-purple myhospital_time">
                住院中，第
                <span>{{index}}</span>天
              </div>
            </el-col>
          </el-row>
          <el-divider class="myhospital_hr"></el-divider>
          <el-row>
            <el-col :span="24">
              <div class="grid-content bg-purple-dark dailylistingIn_time">
                <span>{{dataOnes.ChargeDate}}入院</span>
              </div>
            </el-col>
          </el-row>
        </el-card>
        <!--  -->
        <div class="title" style="margin-top:10px">
          <div class="title-img"></div>
          <div class="title-font">
            <span>基本信息</span>
          </div>
        </div>
        <el-card class="box-card myhospital_LIST">
          <div>
            <el-row>
              <el-col :span="8" class="myhospital_list_left">
                <div class="grid-content bg-purple">患者姓名</div>
                <div class="grid-content bg-purple">科室</div>
                <div class="grid-content bg-purple">医生</div>
                <div class="grid-content bg-purple">流水号</div>
              </el-col>
              <el-col :span="16" class="myhospital_list_right">
                <div class="grid-content bg-purple-light">
                  <span>{{from.name}}</span>
                </div>
                <div class="grid-content bg-purple-light"><span>{{dataOnes.Dept}}</span></div>
                <div class="grid-content bg-purple-light"><span>{{dataOnes.DocName}}</span></div>
                <div class="grid-content bg-purple-light"><span>{{dataOnes.SerialNumber}}</span></div>
              </el-col>
            </el-row>
          </div>
        </el-card>
      </div>
      <!--  -->
    </el-main>
  </el-container>
</template>
<style lang="stylus" scoped>
@import '../../assets/css/outpatient.css';
</style>
<script>
//引入vuex mapState
import { mapState } from "vuex";
export default {
  data() {
    return {
      inTime: [],
      index: "",
      disLeft: false,
      disRight: false,
      borLeft: "",
      borRight: "",
      arrData: [],
      arrDiv: []
    };
  },
  methods: {
    // 日期
    time() {
      var new_Date = new Date(); //当前时间
      var timesStamp = new_Date.getTime(); //获取毫秒
      var day = new_Date.getDay(); //当前系统时间中的星期值
      var weeks = [
        "星期四",
        "星期五",
        "星期六",
        "星期日",
        "星期一",
        "星期二",
        "星期三"
      ];
      var inTime = [];
      for (var i = 0; i < 7; i++) {
        day++;
        var week = weeks[day - 1];
        if (day == 7) {
          day = 0;
        }
        // new Date(timesStamp + 24 * 60 * 60 * 1000 * (i-3) 当前时间+(i+3)
        inTime.push(
          new Date(timesStamp + 24 * 60 * 60 * 1000 * (i - 3))
            .toLocaleDateString()
            .replace(/[年月]/g, "-")
            .replace(/[日上下午]/g, "") + week
        );
      }
      this.inTime = inTime[this.index];
      return inTime;
    },
    //遍历根据日期创建的容器
    arrDivOne() {
      //   this.$nextTicky用于渲染完成后
      this.$nextTick(() => {
        this.msg2 = this.$refs.msgDiv;
        for (let i = 0; i < this.msg2.length; i++) {
          this.msg2[i].style.display = "none";
        }
        this.msg2[this.index].style.display = "block";
      });
    },
    // 日期减一
    left(i) {
      this.index--;
      this.arrDivOne();
      this.time();
      console.log(this.index);

      if (this.index == 0) {
        this.disLeft = true;
        this.borLeft = "bor";
      } else if (this.index > 0) {
        this.disRight = false;
        this.borRight = "";
      }
    },
    right(e) {
      console.log(this.index);
      this.index++;
      this.arrDivOne();
      this.time();
      if (this.index == 6) {
        this.disRight = true;
        this.borRight = "bor";
      } else if (this.index < 6) {
        this.disLeft = false;
        this.borLeft = "";
        // e.currentTarget.style.color = ""; currentTarget 指向本身
      }
    }
  },
  created() {},
  // 调用vuex
  computed: {
    ...mapState(["dataOne", "from"])
  },
  mounted() {
    // 调用vuex store  getdata方法
    this.$store.dispatch("getdata");
    console.log(this.dataOne);

    this.arrDiv = this.time();
    this.index = parseInt(this.time().length / 2);
    this.time();
    this.arrDivOne();
    console.log(this.arrDiv);
    console.log(this.arrData);
  }
};
</script>



